<template>
  <v-ons-page>
    <v-ons-list>
      <v-ons-list-header>Slacker</v-ons-list-header>

      <v-ons-list-item v-for="person in sortedStaff" :key="person.name">
        <div class="center">
          <span class="list-item__title">{{ person.name }}</span>
          <span class="list-item__subtitle">{{ person.last_entry }}</span>
        </div>
      </v-ons-list-item>
    </v-ons-list>

    <v-ons-alert-dialog
      modifier="rowfooter"
      :title="'Foutmelding'"
      :footer="{
        Ok: () => {alertDialogVisible = false; this.setError(false);}
      }"
      :visible.sync="alertDialogVisible"
    >Dit was fout</v-ons-alert-dialog>
  </v-ons-page>
</template>

<script>
import axios from "axios";
import jsonp from "axios-jsonp";
import _ from "lodash";
import moment from "moment";

export default {
  data() {
    return {
      alertDialogVisible: false,
      organizationID: "37192712",
      staff: [
        {
          name: "brent",
          user_id: "511312000002892007",
          color: "green",
          last_entry: "2018-01-01"
        },
        {
          name: "eline",
          user_id: "511312000002730237",
          color: "yellow",
          last_entry: "2018-01-01"
        },
        {
          name: "esther",
          user_id: "511312000000060051",
          color: "purple",
          last_entry: "2018-01-01"
        },
        {
          name: "jesse",
          user_id: "511312000002640061",
          color: "orange",
          last_entry: "2018-01-01"
        },
        {
          name: "kiem",
          user_id: "511312000000499017",
          color: "grey",
          last_entry: "2018-01-01"
        },
        {
          name: "marco",
          user_id: "511312000000633001",
          color: "red",
          last_entry: "2018-01-01"
        },
        {
          name: "misha",
          user_id: "511312000001799001",
          color: "turquoise",
          last_entry: "2018-01-01"
        },
        {
          name: "otto",
          user_id: "511312000000060059",
          color: "pink",
          last_entry: "2018-01-01"
        },
        {
          name: "zaid",
          user_id: "511312000001321121",
          color: "aquamarine",
          last_entry: "2018-01-01"
        }
      ],
      slackDates: [
        {
          key: "today",
          highlight: {
            backgroundColor: "grey"
            // Other properties are available too, like `height` & `borderRadius`
          },
          dates: new Date()
        }
      ]
    };
  },
  computed: {
    sortedStaff: function() {
      return _.orderBy(this.staff, "last_entry");
    },
    _() {
      return _;
    }
  },
  created() {
    this.fetchData().catch(error => {
      this.errorMessage = error;
      console.log(error.message);
    });
  },
  beforeDestroy() {
    this.$store.commit("setSlackDates", this.slackDates);
    this.$store.commit("setStaff", this.staff);
  },
  methods: {
    daysAway: function(LogDate) {
      return moment(LogDate).fromNow();
    },
    async fetchData() {
      if (this.$store.getters.staff[2]) {
        this.staff = this.$store.getters.staff;
        this.slackDates = this.$store.getters.slackDates;
        return;
      }
      const ZohoToken = sessionStorage.getItem("zoho-token");
      if (!ZohoToken) {
        this.alertDialogVisible = true;
        return;
      }
      for (var i = 0; i < this.staff.length; i++) {
        // console.log(this.staff[i].name);
        const url =
          "https://invoice.zoho.com/api/v3/projects/timeentries?authtoken=" +
          ZohoToken +
          "&organization_id=" +
          this.organizationID +
          "&user_id=" +
          this.staff[i].user_id +
          "&sort_column=log_date&sort_order=D";
        let res = await axios({
          url,
          adapter: jsonp
        });
        let lastDate = !res.data.body.time_entries[0]
          ? "2018-01-01"
          : res.data.body.time_entries[0].log_date;
        this.staff[i].last_entry = lastDate;

        // push this date to calendar
        let output = {};
        output.popover = output.highlight = {};
        output.key = output.popover.label = this.staff[i].name;
        output.highlight.backgroundColor = this.staff[i].color;
        output.dates = Date.parse(lastDate);
        this.slackDates.push(output);
      }
    }
  },
  watch: {
    isError: function() {
      if (this.isError === true) {
        // alert('fout!');
        this.alertDialogVisible = true;
        // this.setError(false);
      }
    }
  }
};
</script>

<style scoped>
.page {
  top: 50px;
}
</style>


