import Vue from "vue";
import Vuex from "vuex";
import firebaseAxios from "./axios-auth";
import globalAxios from "axios";

import router from "../router";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    idToken: null,
    userId: null,
    user: null,
    error: false,
    errorMessage: "",
    tokens: {},
    projects: [],
    sProjects: [],
    projectsChart1: {},
    projectsChart2: {},
    devChart1: {},
    devChart2: {},
    supportChart: {},
    helpdeskChart: {},
    salesChart1: {},
    productionChart1: {},
    productionHours: [],
    slackDates: [],
    staff: [],
  },
  mutations: {
    setProjects(state, data) {
      state.projects = data;
    },
    setSProjects(state, data) {
      state.sProjects = data;
    },
    setProject1(state, data) {
      state.projectsChart1 = data;
    },
    setProject2(state, data) {
      state.projectsChart2 = data;
    },
    setDev1(state, data) {
      state.devChart1 = data;
    },
    setDev2(state, data) {
      state.devChart2 = data;
    },
    setSupport(state, data) {
      state.supportChart = data;
    },
    setHelpdesk(state, data) {
      state.helpdeskChart = data;
    },
    setSales1(state, data) {
      state.salesChart1 = data;
    },
    setProduction1(state, data) {
      state.productionChart1 = data;
    },
    setProductionHours(state, data) {
      state.productionHours = data;
    },
    setSlackDates(state, data) {
      state.slackDates = data;
    },
    setStaff(state, data) {
      state.staff = data;
    },
    authUser(state, userData) {
      state.idToken = userData.token;
      state.userId = userData.userId;
      state.user = userData.user;
    },
    storeUser(state, user) {
      state.user = user;
    },
    clearAuthData(state) {
      state.idToken = null;
      state.userId = null;
    },
    setError(state, error) {
      state.error = error;
    },
    setErrorMessage(state, errorMessage) {
      state.errorMessage = errorMessage;
    },
    storeTokens(state, tokens) {
      state.tokens = tokens;
    },
  },
  actions: {
    setLogoutTimer({ commit }, expirationTime) {
      setTimeout(() => {
        commit("clearAuthData");
      }, expirationTime * 1000);
    },
    signup({ commit, dispatch }, authData) {
      firebaseAxios
        .post("/signupNewUser?key=AIzaSyBkcvzmwkGvgOHPVnh6IbaevVmucacNJi8", {
          email: authData.email,
          password: authData.password,
          returnSecureToken: true,
        })
        .then(res => {
          console.log(res);
          commit("authUser", {
            token: res.data.idToken,
            userId: res.data.localId,
          });
          const now = new Date();
          const expirationDate = new Date(
            now.getTime() + res.data.expiresIn * 1000
          );
          localStorage.setItem("token", res.data.idToken);
          localStorage.setItem("userId", res.data.localId);
          localStorage.setItem("expirationDate", expirationDate);
          dispatch("storeUser", authData);
          dispatch("setLogoutTimer", res.data.expiresIn);
        })
        .catch(error => console.log(error));
    },
    login({ commit, dispatch }, authData) {
      firebaseAxios
        .post("/verifyPassword?key=AIzaSyBkcvzmwkGvgOHPVnh6IbaevVmucacNJi8", {
          email: authData.email,
          password: authData.password,
          returnSecureToken: true,
        })
        .then(res => {
          const now = new Date();
          const expirationDate = new Date(
            now.getTime() + res.data.expiresIn * 1000
          );
          localStorage.setItem("token", res.data.idToken);
          localStorage.setItem("userId", res.data.localId);
          localStorage.setItem("expirationDate", expirationDate);
          commit("authUser", {
            token: res.data.idToken,
            userId: res.data.localId,
          });
          dispatch("setLogoutTimer", res.data.expiresIn);
          dispatch("fetchTokens").then(() => {
            router.replace("/Hello");
          });
        })
        .catch(error => {
          commit("setError", true);
          commit("setErrorMessage", error.message);
          console.log(error);
        });
    },
    tryAutoLogin({ commit, dispatch }) {
      const expirationDate = Date.parse(localStorage.getItem("expirationDate"));
      const now = new Date();
      if (now >= expirationDate) {
        return;
      }
      const token = localStorage.getItem("token");
      if (!token) {
        return;
      }
      const userId = localStorage.getItem("userId");
      commit("authUser", {
        token: token,
        userId: userId,
      });
      const zohoToken = sessionStorage.getItem("zoho-token");
      if (!zohoToken) {
        dispatch("fetchTokens");
      }
    },
    logout({ commit }) {
      commit("clearAuthData");
      localStorage.removeItem("expirationDate");
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      sessionStorage.clear();
      router.replace("/signin");
    },
    storeUser({ commit, state }, userData) {
      if (!state.idToken) {
        return;
      }
      globalAxios
        .post("/users.json" + "?auth=" + state.idToken, userData)
        .then(res => console.log(res))
        .catch(error => console.log(error));
    },
    fetchUser({ commit, state }) {
      if (!state.idToken) {
        return;
      }
      globalAxios
        .get("/users.json" + "?auth=" + state.idToken)
        .then(res => {
          // console.log(res);
          const data = res.data;
          const users = [];
          for (let key in data) {
            const user = data[key];
            user.id = key;
            users.push(user);
          }
          // console.log(users);
          commit("storeUser", users[1]);
        })
        .catch(error => console.log(error));
    },
    fetchTokens({ commit, state }) {
      return new Promise((resolve, reject) => {
        globalAxios
          .get("/tokens.json" + "?auth=" + state.idToken)
          .then(res => {
            for (let key in res.data) {
              sessionStorage.setItem(key, res.data[key]);
            }
            commit("storeTokens", res.data);
            resolve();
          })
          .catch(error => console.log(error));
      });
    },
  },
  getters: {
    user(state) {
      return state.user;
    },
    isAuthenticated(state) {
      return state.idToken !== null;
    },
    isError: state => state.error,
    errorMessage(state) {
      return state.errorMessage;
    },
    projects(state) {
      return state.projects;
    },
    sProjects(state) {
      return state.sProjects;
    },
    projectsChart1(state) {
      return state.projectsChart1;
    },
    projectsChart2(state) {
      return state.projectsChart2;
    },
    devChart1(state) {
      return state.devChart1;
    },
    devChart2(state) {
      return state.devChart2;
    },
    supportChart(state) {
      return state.supportChart;
    },
    helpdeskChart(state) {
      return state.helpdeskChart;
    },
    salesChart1(state) {
      return state.salesChart1;
    },
    productionChart1(state) {
      return state.productionChart1;
    },
    productionHours(state) {
      return state.productionHours;
    },
    slackDates(state) {
      return state.slackDates;
    },
    staff(state) {
      return state.staff;
    },
  },
});



