<template>
  <div id="signin">
    <div class="signin-form">
      <v-ons-list>
        <v-ons-list-item>
          <div class="center">
            <v-ons-input placeholder="E-mail" float v-model="email"></v-ons-input>
          </div>
        </v-ons-list-item>
        <v-ons-list-item>
          <div class="center">
            <v-ons-input placeholder="Wachtwoord" float v-model="password"></v-ons-input>
          </div>
        </v-ons-list-item>
        <v-ons-list-item>
          <div class="center">
            <v-ons-button style="margin: 6px 0" @click="onSubmit">Verzend</v-ons-button>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </div>
    <v-ons-alert-dialog
      modifier="rowfooter"
      :title="'Foutmelding'"
      :footer="{
        Ok: () => {alertDialogVisible = false; this.setError(false);}
      }"
      :visible.sync="alertDialogVisible"
    >Dit was fout</v-ons-alert-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  data() {
    return {
      email: "",
      password: "",
      alertDialogVisible: false
    };
  },
  computed: {
    ...mapGetters(["isError"])
  },
  methods: {
    ...mapMutations(["setError"]),
    onSubmit() {
      const formData = {
        email: this.email,
        password: this.password
      };
      this.$store.dispatch("login", {
        email: formData.email,
        password: formData.password
      });
    }
  },
  watch: {
    isError: function() {
      if (this.isError === true) {
        // alert('fout!');
        this.alertDialogVisible = true;
        // this.setError(false);
      }
    }
  }
};
</script>

<style scoped>
</style>


