<template id="main">
  <v-ons-splitter>
    <v-ons-toolbar>
      <div class="left">
        <v-ons-toolbar-button @click="toggleMenu">
          <v-ons-icon icon="ion-navicon, material:md-menu"></v-ons-icon>
        </v-ons-toolbar-button>
      </div>
      <div class="center">{{ title }}</div>
    </v-ons-toolbar>
    <v-ons-splitter-side swipeable width="150px" collapse side="left" :open.sync="openSide">
      <v-ons-page>
        <v-ons-list>
          <v-ons-list-item
            v-for="page in pages"
            v-bind:key="page"
            tappable
            modifier="chevron"
            @click="currentPage = page; openSide = false; $router.push(page);"
          >
            <div class="center">{{ page }}</div>
          </v-ons-list-item>
        </v-ons-list>
      </v-ons-page>
    </v-ons-splitter-side>

    <v-ons-splitter-content>
      <router-view></router-view>
    </v-ons-splitter-content>
  </v-ons-splitter>
</template>


<script>
export default {
  data() {
    return {
      currentPage: "home",
      pages: ["Een", "Twee", "Hello", "Slacker"],
      title: "Management dashboard",
      openSide: false
    };
  },
  methods: {
    toggleMenu() {
      this.openSide = !this.openSide;
    }
  }
};
</script>

<style scoped>
router-view {
  top: 160px;
  left: 10px;
}
</style>




